@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.culture {
  &-page {
    padding-bottom: 40px;

    &__subtitle {
      font-family: var.$font-family;
      color: var.$secondary;
    }
  }

  &__images {
    margin-bottom: 96px;
    gap: 24px 0;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.tablet {
      margin-bottom: 64px;
    }

    @include mixin.small-tablet {
      margin-bottom: 48px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__tabs {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px 16px;
    margin-bottom: 96px;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.tablet {
      margin-bottom: 64px;
    }

    @include mixin.small-tablet {
      margin-bottom: 48px;
    }
  }

  &__tab {
    border: 1px solid var.$secondary;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.5px;
    padding: 12px 32px;
    transition: 0.3s linear;
    background: transparent;
    color: var.$secondary;


    &:hover {
      background: var.$secondary;
      color: var.$light;
    }

    @include mixin.small-tablet {
      flex-grow: 1;
    }
  }

  &__block {
    margin-bottom: 96px;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.tablet {
      margin-bottom: 64px;
    }

    @include mixin.small-tablet {
      margin-bottom: 48px;
    }
  }

  &__block-title {
    margin-bottom: 48px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }
}

.row-tab {
  &__heads {
    padding-right: 64px;
  }

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    padding: 24px 32px;
    border-top: 1px solid #E4E4E4;
    cursor: pointer;
    transition: 0.3s linear;

    &:first-child {
      border-top-color: transparent;
    }

    &--active {
      background: #FDF6EB;
      border-top-color: transparent;
      border-radius: 32px 0px;

      &+.row-tab__head {
        border-top-color: transparent;
      }
    }
  }

  &__head-title {
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 1.66;
    color: var.$secondary;
    margin-bottom: 8px;
  }

  &__head-text {
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 1.66;
    letter-spacing: 0.35px;
    flex-grow: 1;
    text-align: left;
    color: var.$secondary;
  }

  &__head-btn {
    flex-shrink: 0;
    display: flex;
    width: 48px;
    height: 48px;
    padding: 12px;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background: rgba(var.$secondary, 0.1);
    transition: 0.3s linear;

    path {
      fill: var.$secondary;
      transition: 0.3s linear;
    }
  }

  &__head--active &__head-btn {
    background: var.$primary;

    path {
      fill: var.$light;
    }
  }

  &__body-item {
    min-height: 100%;
    display: none;

    &--active {
      display: block;
    }
  }

  &__body-title {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-style: normal;
    font-weight: 700;
    line-height: 1.66;
    margin-bottom: 24px;
  }

  &__body-text {
    color: var.$gray;
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.66;
    margin-bottom: 24px;
  }

  &__body-img {
    width: 100%;

    img {
      width: 100%;
      object-fit: cover;
    }
  }
}

.culture-slider {
  min-height: 100%;

  &__item-img {
    text-align: center;
    max-width: 630px;
    max-height: 420px;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-self: center;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @include mixin.tablet {
      aspect-ratio: 1.48;
    }
  }

  &__control {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    margin-top: 32px;
  }

  &__counter {
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 1.66;
    letter-spacing: 0.83px;
    color: var.$secondary;
  }

  &__btn {
    padding: 8px 16px;

    svg {
      width: 24px;
      height: 24px;

      path {
        stroke: var.$gray2;
      }
    }

    &--right {
      transform-origin: center;
      transform: rotateZ(180deg);
    }
  }
}

.collapse-list {
  .collapse-item {
    margin-bottom: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid #E4E4E4;

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;
      border-bottom: unset;
    }

    &.active {
      padding-bottom: 17px;
      border-bottom-color: transparent;
    }

    &__head {
      display: flex;
      justify-content: space-between;
      align-items: center;

      &.collapsed .collapse-item__btn {
        transform: rotateZ(0deg);
        background: rgba(var.$secondary, 0.1);

        svg {
          path {
            stroke: var.$secondary;
          }
        }
      }
    }

    &__btn {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.3s linear;
      transform-origin: center;
      transform: rotateZ(180deg);
      background: rgba(var.$primary, 0.1);
      flex-shrink: 0;

      svg {
        width: 14px;
        height: 7px;

        path {
          stroke: var.$primary;
        }
      }
    }

    &__body {
      padding-top: 16px;
    }

    &__text {
      font-family: var.$font-family;
      font-weight: 300;
      font-size: 1.8rem;
      line-height: 1.66;
      color: var.$gray;
      margin-bottom: 16px;
    }
  }
}

.values {
  &__list {
    gap: 48px 0;

    @include mixin.small-tablet {
      gap: 32px 0;
    }
  }

  .value {
    &__num {
      position: relative;
      width: 96px;
      height: 96px;
      display: block;
      border-radius: 50%;
      box-sizing: border-box;
      padding: 12px;
      font-family: var.$font-family;
      font-size: 6.4rem;
      font-weight: 700;
      line-height: 1;
      letter-spacing: -1px;
      background: linear-gradient(-45deg, #E28B27, rgba(#F18509, 0.34));
      display: flex;
      justify-content: center;
      align-items: center;
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
      margin-bottom: 32px;

      @include mixin.small-tablet {
        width: 72px;
        height: 72px;
        padding: 4px;
        font-size: 4.8rem;
        line-height: 1.33;
        margin-bottom: 16px;
      }

      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: var.$tertiary;
        border-radius: 50%;
      }
    }

    &__title {
      font-weight: 700;
      line-height: 1.66;
      margin-bottom: 16px;

      @include mixin.small-tablet {
        margin-bottom: 8px;
      }
    }

    &__text {
      font-family: var.$font-family;
      font-size: 1.8rem;
      font-weight: 300;
      line-height: 1.66;
      letter-spacing: 0.35px;

      @include mixin.small-tablet {
        margin-bottom: 8px;
        color: var.$gray;
      }
    }
  }
}

.lessons {
  display: block;

  @include mixin.tablet {
    display: none;
  }
}

.lessons-mob {
  display: none;

  @include mixin.tablet {
    display: block;
  }
}

.conditions {
  &__list {
    gap: 48px 0;

    @include mixin.tablet {
      gap: 32px 0;
    }
  }

  .condition {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 32px;

    @include mixin.tablet {
      align-items: flex-start;
      flex-direction: column;
      gap: 16px;
    }

    &__icon {
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: var.$tertiary;
      border-radius: 50%;
      width: 100px;
      height: 100px;

      @include mixin.tablet {
        width: 72px;
        height: 72px;

        img {
          width: 34px;
        }
      }
    }

    &__text {
      max-width: 493px;
      box-sizing: border-box;
      padding-right: 45px;

      @include mixin.tablet {
        padding-right: 0;
      }
    }
  }
}

.holidays {
  display: block;

  @include mixin.tablet {
    display: none;
  }
}

.holidays-mob {
  display: none;

  @include mixin.tablet {
    display: block;
  }
}

.holidays-slider {
  &__item-img {
    text-align: center;
    max-width: 630px;
    max-height: 420px;
    height: 100%;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__control {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    margin-top: 32px;
  }

  &__counter {
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 1.66;
    letter-spacing: 0.83px;
    color: var.$secondary;
  }

  &__btn {
    padding: 8px 16px;

    svg {
      width: 24px;
      height: 24px;

      path {
        stroke: var.$gray2;
      }
    }

    &--right {
      transform-origin: center;
      transform: rotateZ(180deg);
    }
  }
}

.vacancies {
  &__head {
    margin-bottom: 48px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 24px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }

  &__controls {
    display: flex;
    gap: 20px
  }

  &__tags {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 12px;
    margin-bottom: 48px;
    overflow-y: auto;
    scrollbar-width: none;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__tag {
    padding: 8px 24px;
    color: var.$gray2;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    transition: 0.3s linear;
    cursor: pointer;
    background: var.$tertiary;

    &:hover,
    &--active {
      color: var.$light;
      background: var.$primary;
    }
  }

  &__list {
    margin-bottom: 48px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
  }
}

.vacancies-filter {
  color: var.$gray2;

  @include mixin.small-tablet {
    display: none;
  }

  &__dropdown-btn {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 10px 16px;

    span {
      color: var.$gray2;
      font-family: var.$font-family;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      letter-spacing: 0.5px;
    }

    svg {
      width: 16px;
      fill: var.$secondary;
    }
  }

  &__list {
    a {
      white-space: nowrap;
    }
  }
}

.vacancy {
  padding: 32px;
  border-bottom: 1px solid rgba(var.$secondary, 0.20);
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 32px;
  box-sizing: border-box;
  text-align: left;
  height: auto;

  @include mixin.phone {
    flex-wrap: wrap;
    gap: 16px;
  }

  &:last-child {
    border-bottom: unset;
  }

  &__number {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: 1.4;
  }

  &__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-grow: 1;
    gap: 16px;
  }

  &__content {
    flex-grow: 1;
  }

  &__title {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-style: normal;
    font-weight: 700;
    line-height: 1.66;
    letter-spacing: 0.833px;
    margin-bottom: 16px;
  }

  &__text {
    color: rgba(var.$secondary, 0.50);
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 300;
    line-height: 1.66;
  }

  &__more {
    display: flex;
    width: 48px;
    height: 48px;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background: rgba(var.$secondary, 0.10);
    flex-shrink: 0;

    svg {
      width: 24.664px;
      height: 21.453px;
    }
  }
}

.career {
  &__list {
    gap: 16px 0;
  }

  .badge {
    gap: 24px;
  }
}